<template>
  <div class="film-box" style="height:100%">
    <div class="content" :style="{ minHeight: curHeight+'px'}">
      <!-- 头部区域 -->
      <div style="padding-top: 1rem;">
        <div class="hedeBox" style="position: fixed;left: 0;top: 0;width: 100%;z-index: 10;">
          <nav-head :title="title" ></nav-head>
        </div>
        <!-- 手机号： -->
        <div class="person-card-box">
          <!-- right-icon="close" -->
            <van-field v-model="parameter.tel" label="手机号：" placeholder="请输入手机号" />
        </div>
        <div class="getcode-box" style="margin-top: 0">
            <van-field v-model="parameter.lockcode" label="" placeholder="请输入验证码" />
            <span @click="sendCode()" class="getcode-btn">{{vcodeBtnName}}</span>
        </div>
        <!-- 确认密 码 -->
        <div class="person-card-box" style="margin-top: 0">
          <!-- 、right-icon="closed-eye" -->
            <van-field v-model="parameter.pass" label="新 密 码：" placeholder="" type="password"  />
        </div>
        <!-- 确定添加按钮 -->
        <div class="bottom-btn" @click="userBut()">确认</div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { telcode, ajaxsms, forgetpassword } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import NavHead from '@/components/head.vue'
import { Field, Toast } from 'vant'
Vue.use(Field).use(Toast)
export default {
  components: { NavHead },
  data () {
    return {
      value: '',
      title: '修改密码',
      vcodeBtnName: '获取验证码',
      datas: { // 获取验证码
        tel: ''
      },
      validity: { // code验证
        tel: '',
        lockcode: ''
      },
      parameter: {
        tel: '',
        pass: '', // 密码
        lockcode: ''
      },
      curHeight: ''
    }
  },
  created () {
    this.beforeMount()
  },
  methods: {
    userBut () {
      var that = this
      if (that.parameter.tel === '') {
        that.$toast({ message: '手机号不能为空', duration: 1500 })
        return
      }
      if (!(/^[0-9]{11}$/.test(that.parameter.tel))) {
        that.$toast({ message: '请输入正确手机号', duration: 1500 })
        return
      }
      if (that.parameter.pass === '') {
        that.$toast({ message: '请输入密码', duration: 1500 })
        return
      }
      if (that.parameter.lockcode === '') {
        that.$toast({ message: '请输入验证码', duration: 1500 })
        return
      }
      this.verifySms()
    },
    sendCode () {
      var that = this
      if (that.parameter.tel === '') {
        that.$toast({ message: '手机号不能为空', duration: 1500 })
        return
      }
      if (!(/^[0-9]{11}$/.test(that.parameter.tel))) {
        that.$toast({ message: '请输入正确手机号', duration: 1500 })
        return
      }
      if (that.vcodeBtnName !== '获取验证码' && that.vcodeBtnName !== '重新发送') { return }
      that.datas.tel = that.parameter.tel
      var data = objKeySort(that.datas)
      var token = sgin(data)
      that.datas.token = token
      telcode(this.datas).then(res => {
        if (res.code === 0) {
          that.$toast({ message: '短信已发送，请注意查收', duration: 1500 })
          that.countNum = 120
          that.countDownTimer = setInterval(function () { this.countDown() }.bind(this), 1000)
          delete this.datas.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        that.vcodeBtnName = '重新发送'
        delete this.datas.token
      })
    },
    countDown () {
      if (this.countNum < 1) {
        clearInterval(this.countDownTimer)
        this.vcodeBtnName = '重新发送'
        return
      }
      this.countNum--
      this.vcodeBtnName = this.countNum + '秒重发'
    },
    verifySms () {
      var that = this
      that.validity.tel = that.parameter.tel
      that.validity.lockcode = that.parameter.lockcode
      var data = objKeySort(that.validity)
      var token = sgin(data)
      that.validity.token = token
      ajaxsms(that.validity).then(res => {
        console.log(res, '验证成功')
        if (res.code === 0) {
          // if (that.code === '') {
          //   that.parameter.code = 0
          // } else {
          //   that.parameter.code = that.code
          // }
          var data = objKeySort(this.parameter)
          var token = sgin(data)
          this.parameter.token = token
          that.transactionList(this.parameter)
          delete this.validity.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        delete this.validity.token
      })
    },
    // 最后提交
    transactionList (parameter) {
      var that = this
      forgetpassword(parameter).then(res => {
        console.log(res, '注册成功')
        if (res.code === 0) {
          that.$toast({ message: '修改密码成功', duration: 1500 })
          that.parameter.tel = ''
          that.parameter.pass = ''
          that.parameter.lockcode = ''
          delete this.parameter.token
        }
      }).catch((err) => {
        that.$toast({
          message: err,
          duration: 1500
        })
        that.parameter.tel = ''
        that.parameter.pass = ''
        that.parameter.lockcode = ''
        delete this.parameter.token
      })
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
      console.log(this.curHeight, 'this.curHeight')
    }
  }
}
</script>
